/*  Menu Icon  */
.menu-icon {
  position: relative;
  width: 40px;
  height: 40px;
  display: inline-block;
  align-self: center;
  cursor: pointer; }
  @media all and (min-width: 768px) {
    .menu-icon {
      display: none; } }
  .menu-icon span {
    position: absolute;
    left: 5px;
    right: 5px;
    height: 2.5px;
    width: 75%;
    background-color: #fff;
    transition: all 300ms ease-in-out;
    transform: rotate(0deg);
    opacity: 1; }
    .menu-icon span:nth-child(1) {
      bottom: 30px; }
    .menu-icon span:nth-child(2) {
      bottom: 20px; }
    .menu-icon span:nth-child(3) {
      bottom: 10px; }
  .menu-icon:hover span,
  .menu-icon:focus span,
  .menu-icon:active span {
    background: #eee; }

.menu-state {
  display: none; }
  .menu-state:checked ~ .menu-icon span:nth-child(1),
  .menu-state:checked ~ .menu-icon span:nth-child(3) {
    top: 20px;
    transform: translate(0, -50%);
    opacity: 0.9; }
  .menu-state:checked ~ .menu-icon span:nth-child(1) {
    transform: rotate(45deg); }
  .menu-state:checked ~ .menu-icon span:nth-child(3) {
    transform: rotate(-45deg); }
  .menu-state:checked ~ .menu-icon span:nth-child(2) {
    opacity: 0; }

